// [id$="layout-center"],
[id*="layout-left"],
[id*="layout-right"] {
  .tl-slide-content {
    display: flex !important;
  }
}

[id*="layout-right"] {
  .tl-media {
    order: 1;
  }

  .tl-text {
    order: 2;
  }
}

[id*="layout-left"] {
  .tl-text {
    order: 1;
  }

  .tl-media {
    order: 2;
  }
}

